:root {
  
}

::selection {
  background-color: #FE6B8B;
  color: #fff;
}

h1, h2{
  font-family: 'Montserrat', sans-serif;
}

.bg-off-white {
  background-color: #f8f6f8;
}



#subHeading {
  opacity: 0;
  /* margin-top: -50px; */
  transition: all 0.3s ease-in-out;
}

#cursor {
  -webkit-animation: blink 0.6s alternate infinite;
          animation: blink 0.6s alternate infinite;
  font-weight: 500;
}

@-webkit-keyframes blink {
  0%, 49% {
    opacity: 0;
  } 
  50%, 100% {
    opacity: 1;
  }
}

@keyframes blink {
  0%, 49% {
    opacity: 0;
  } 
  50%, 100% {
    opacity: 1;
  }
}
